@media only device and (max-width:768px)
{
	.index .container{
		margin-left:30px !important;
		margin-right:30px !important;
		display:flex;
		justify-content: center;
	}
	.img-fluid
	{
		height:450px;
		width:660px;
	}
	#caseul
	{
		display:flex;
		justify-content: space-between;
	}
	#caseul > li >div
	{
		background-color:white;
		width:250px;
	}
	#caseul > li:hover:nth-child(1) > div
	{
		background-color:#B9DDFB;
	}

	#caseul > li:hover:nth-child(2) >div
	{
		background-color:#E1F8D5;
	}
	#caseul > li:hover:nth-child(3) >div
	{
		background-color:#D9DED8;
	}
	#headtitle > div:hover
	{
		background-color:#D4E8C9;
	}
	#headtitle > div
	{
		padding-top:30px;
		position:relative;
		padding-bottom:10vw !important
	}
	#a
	{
		position:relative;
		left:-14vw;
	}
	#b
	{
		position:relative;
		left:-17vw;
	}
	#c
	{
		position:relative;
		left:-20vw;
		bottom:0vw;
	}
	#d
	{
		position:relative;
		left:51vw;
		bottom:16vw;
	}
	#e
	{
		display:none;
	}
	#f
	{
		position:relative;
		left:27vw;
		bottom:16vw;
	}


	#resultnav > ul
	{
		display:flex;
		justify-content:space-between;
	}
	#headtitle > div > li
	{
		position:relative;
		top:-10px;
		font-size:4vw;
	}
	#headtitle
	{
		color:#9AA295;
		font-weight:bold;
	}
	#updatecard{
		width:950px;
		position:relative;
		right:5vw;
	}
	#myfirstul{
		width:200px;
	}
	#headingtotal {
		font-size:5vw;
	}
	#caseul > li >div >b >center >span{
		font-size:3.7vw;
	}
	#activecase,#recovered,#death{
		font-size:3.7vw;
	}
	#heighttest{
		position:relative;
		right:6vw;
	}


}

