@media(max-width:4096px)
{
	#mylogincard
	{
		background-color: #E8F1E2;
	}
	.card-header
	{
		border:none;
		background-color: #E8F1E2;
	}
	.card-body > div > input
	{
		width:100%;
		border:none;
	}
	.card-body >div >input:focus
	{
		outline:none;
		box-shadow: 0 0 0 0;
	}
	.card-body >div >a
	{
		background-color: red;
		color:white;width:100%;
	}
	#loginerror
	{
		background-color:red;
		color:white;
	}

}
