@media (max-width:4096px){
	#myjumbo > input{
		background-color:#E2EDEC;

	}
	#myjumbo > input:focus{
		outline:1px solid;
	}
	#updatebtn
	{
		color:white;
		background-color:red;
		width:100%;
	}
	#deletetest{
		position:relative;
		float:right;
	}
	#myeditpencil{
		font-size:25px;
		color:black;
