{
	#feedjumbo
	{
		color:#F9F5F7;
	}
	#feedbackjumbo
	{
		background-color:#E8A278;
		float:center;

	}
	#feedjumbo >h4,#feedjumbo > span
	{
		color:black;
		font-weight:lighter;
	}
	#responsealert{
		background-color:green;
