@media (max-width:4096px){
	#myid7{
		background-color:white;
	}
	#myid7 > ul{
		justify-content: right;
	}
	#myid7 > ul >li >a > i{
		color:#E6DFD8;
	}
	div#dbform > input{
		background-color:#F6F3F0;
		width:700px;
		padding-left:30px;

	}
	.modal-title{
		position:relative;
		left:180px;
	}
	#dbform >input:focus{
		outline:hidden;
	}
	#dbform > i{
		position:relative;
		left:20px;
		font-size:18px;
	}
	#firstmaker{
		background-color:red;
		outline-style:solid;
		color:white;
		font-weight:bold;
		outline-color:black;
		width:50%;
	}
	.list-group{
		height:100px;
	}
	.list-group > li:hover{
		background-color:#E6E1C8;
	}
	.list-group > li{
		width:700px;
		position:relative;
		left:15px;
	}
	#liststyletest{
		justify-content: center;
	}
	.list-group > li >a{
		width:100%;
		text-align:left;
	}
	#spintesting > div{
		position:relative;
		left:400px;
	}
	#spintesting > span{
		position:relative;
		left:385px;
	}

	#testingpleasejumbo{
		width:700px;
		position:relative;
		left:15px;
	}
	#secondmaker{
		background-color:green;
		color:white;
		font-weight:bold;
		outline-color:black;
		outline-style:solid;
		width:50%;
	}
	#navtest{
		display:flex;
	}
	#navtest > ul >li >a{
				text-align:center;

	}
	#navtest > ul>li>a:hover{
		transition:0.2s background-color;

		background-color: black;
		border-radius:50%;
		color:white;
	}


}
