@media (max-width:4096px){
	#adminjumbo{
		 background-color:#221A56;
		 height:1000px;
		 width:100%;
		 overflow-x:hidden;
	}
	#adminjumbo > ul >li>a:hover{
		background-color:#F6B774;
		color:black;
	}
	#testjumbo > li >a[aria-selected="true"]{
		background-color:#F6B774;
		color:black;
	}
	#adminjumbo > ul{
		border:none;
	}
	#adminjumbo > ul > li >a{
		color:white;
		position:relative;
		padding-left:20px;
	}
	.img-fluid{
		height:100px;
		width:100px;
	}
	#admintitle{
		font-weight:bold;
		color:white;
		font-weight:bold;
	}
	#adminjumbo > ul >li{
		margin-bottom: 10px;
	}
	#adminjumbo > ul>li>a>i{
		font-size:20px;
		color:white;
		padding-left:5px;
	}

}
