@media (max-width:4096px){

	#admincard{
		background-color:#4B5C65;
	}
	#admincard >.card-header{
		background-color:#4B5C65;
		color:white;
	}
	#admincard > .card-body >.form-group > input{
		width:100%;
			padding-left:30px;

	}
	#admincard > .card-body >.form-group > input:focus{
		outline:none;
		background-color:#F6F3F0;
	}
	#admincard > .card-body >.form-group >i{
		font-size:25px;
		border:1px;
		position:relative;
		top:32px;
		left:5px;
	}
	#admincard > .card-body >.form-group > a{
		background-color:blue;
		color:white;
	}

}
